/* eslint-disable no-undef */
const burger = document.querySelector('.burger');

if (burger) {
  const menu = document.querySelector('.header__left');
  const nav = document.querySelector('.nav');
  const menuLinks = menu.querySelectorAll('.nav__list-link');

  const changeBurger = () => {
    burger.classList.toggle('burger--active');
    menu.classList.toggle('header__left--active');
    nav.classList.toggle('nav--active');
    document.body.classList.toggle('stop-scroll');
  };

  burger.addEventListener('click', () => {
    changeBurger();
  });

  menuLinks.forEach((el) => {
    el.addEventListener('click', changeBurger);
  });

  document.addEventListener('click', (e) => {
    if (!e.target.classList.contains('burger') && !e.target.closest('.header__left') && burger.classList.contains('burger--active')) changeBurger();
  });

  document.addEventListener('keydown', (e) => {
    if (e.keyCode === 27) changeBurger();
  });
}
